.orders-page {
  min-height: 100vh;
  background-color: var(--background);
  padding-bottom: 80px;
}

.back-button {
  background: none;
  border: none;
  color: var(--text-primary);
  font-size: 20px;
  cursor: pointer;
  padding: 8px 12px;
  margin-right: 12px;
  transition: all 0.3s ease;
  border-radius: 8px;
}

.back-button:hover {
  background: var(--background-light);
  color: var(--primary-color);
}

.no-orders {
  text-align: center;
  padding: 60px 20px;
  background: var(--white);
  border-radius: 16px;
  margin-top: 40px;
  border: 1px solid var(--border-color);
}

.no-orders-icon {
  font-size: 64px;
  margin-bottom: 20px;
}

.no-orders h3 {
  font-size: 20px;
  color: var(--text-primary);
  margin-bottom: 8px;
  font-weight: 700;
}

.no-orders p {
  color: var(--text-secondary);
  margin-bottom: 24px;
  font-size: 14px;
}

.orders-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 20px;
}

.order-card-modern {
  background: var(--white);
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.3);
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
  cursor: pointer;
}

.order-card-modern:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.4);
  border-color: var(--primary-color);
}

.order-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border-color);
}

.order-card-title h3 {
  font-size: 18px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 4px;
}

.order-date {
  font-size: 12px;
  color: var(--text-light);
}

.status-badge-modern {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  white-space: nowrap;
}

.status-pending {
  background: var(--primary-light);
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.status-cooking {
  background: var(--primary-light);
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.status-ready {
  background: var(--primary-light);
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.status-delivery {
  background: var(--primary-light);
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.status-delivered {
  background: var(--primary-light);
  color: var(--success);
  border: 1px solid var(--success);
}

.status-cancelled {
  background: rgba(211, 47, 47, 0.1);
  color: var(--error);
  border: 1px solid var(--error);
}

.order-card-body {
  margin-bottom: 16px;
}

.order-info-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
  font-size: 14px;
}

.order-info-row:last-child {
  margin-bottom: 0;
}

.info-icon {
  font-size: 16px;
  width: 20px;
  text-align: center;
}

.info-text {
  color: var(--text-secondary);
  flex: 1;
}

.order-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  border-top: 1px solid var(--border-color);
}

.order-items-preview {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  flex: 1;
}

.item-tag {
  padding: 4px 10px;
  background: var(--primary-light);
  color: var(--primary-color);
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  border: 1px solid var(--primary-color);
}

.order-total-modern {
  font-size: 18px;
  font-weight: 700;
  color: var(--primary-color);
}

.order-action {
  margin-top: 12px;
}

.loading {
  text-align: center;
  padding: 60px 20px;
  color: var(--text-secondary);
  font-size: 16px;
}

.error {
  color: var(--error);
  padding: 12px;
  background: rgba(211, 47, 47, 0.1);
  border-radius: 8px;
  margin-bottom: 20px;
  border-left: 4px solid var(--error);
}
